
.pixi-app{
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}
.Back-Button{
  bottom: 45px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: absolute;
}

.Back-Button a{
  opacity: 0.4;
  color: white;
  font-family: 'VCR OSD Mono';
  font-size: 24px;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  transition: opacity 250ms ease-out, letter-spacing 250ms cubic-bezier(0, 0, 0, 1);
}

.Back-Button a:hover{
  letter-spacing: 5px;
  opacity: 0.8;
}

@media only screen 
  and (min-device-width: 320px) 
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) {
  .Back-Button{
    bottom: 25px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    position: absolute;
  }
}
.About .Scroll{
  position: absolute;
  left: 50%;
  top: 15%;
  bottom: 15%;
  width: 65%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  overflow-y: scroll;
}

.Scroll span{
  color: white;
  font-family: 'VT323', monospace;
  font-size: 25px;
}

@media only screen and (max-device-width: 480px) {
  .Scroll span{
    font-size: 20px;
  }
}

@media only screen 
  and (min-device-width: 320px) 
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) {
  .About .Scroll{
    bottom: 20%;
  }
}
.Main{
  position: absolute;
  top: 50%;
  left: 5%;
  right: 5%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  z-index: 1;
}

.Main .Name{
  font-family: 'VCR OSD Mono';
  letter-spacing: 1px;
  font-size: 40px;
  color: white;
}

.Main .Small{
  font-family: 'VCR OSD Mono';
  font-size: 25px;
  color: rgb(160, 160, 160);
}

.No-Select{
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
      -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
}

.Routes{
  margin-top: 45px;
}

.Routes a{
  color: white;
  font-family: 'VT323', monospace;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-size: 25px;
  opacity: 0.5;
  transition: opacity 250ms ease-out, letter-spacing 250ms cubic-bezier(0, 0, 0, 1);
}

.Routes a:hover{
  opacity: 0.9;
  letter-spacing: 3px;
}

.Credit{
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-bottom: 20px;
  color: white;
  font-family: 'VT323', monospace;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-size: 20px;
  opacity: 0.2;
}

.Credit:hover{
  opacity: 0.3;
}

@media only screen and (max-device-width: 480px) {
  .Main .Name{
    font-size: 30px;
  }
  .Main .Small{
    font-size: 15px;
  }

  .Routes a{
    font-size: 25px;
  }

  .Credit{
    font-size: 15px;
  }
}

@media only screen and (max-device-width: 320px) {
  .Main .Name{
    font-size: 25px;
  }
}
.Portfolio{
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.Portfolio span{
  color: white;
  font-family: 'VT323', monospace;
  font-size: 25px;
}
html,
body,
#root {
    height: 100%;
    width: 100%;
}

body {
  margin: 0;
  padding: 0;
}

.anim{
  opacity: 0;
  transition: all 250ms ease-out;
}

.in {
  opacity: 1;
}

.animated-page-wrapper {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
}
